import { Icon } from 'assets/svg';
import cx from 'classnames';
import PrimaryButton from 'components/PrimaryButton';
import { useWindowSize } from 'hooks';
import { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { MENU, MenuItemType } from 'utils/constants';
import { isLargerTablet } from 'utils/helper';

function Header() {
  const { t } = useTranslation();
  const [width] = useWindowSize();
  const [collapseState, setCollapseState] = useState(false);

  const handleCollapseMenu = () => {
    setCollapseState(old => !old);
  };

  useEffect(() => {
    if (isLargerTablet(width)) {
      setCollapseState(false);
    }
  }, [width]);

  return (
    <div className={cx(['header', collapseState ? 'active' : ''])}>
      <div className="container">
        <div
          className={cx(['header__container', collapseState ? 'active' : ''])}
        >
          <div className="menu">
            <Link to="/">
              <Icon.Logo />
            </Link>
            <ul
              className="nav"
              style={{
                height: collapseState
                  ? 42 * (MENU.length - 1)
                  : isLargerTablet(width)
                    ? 'auto'
                    : 0,
              }}
            >
              {MENU.filter((_, i) => i !== 0).map(
                (m: MenuItemType, i: number) => (
                  <li key={i}>
                    <Link className="color-primary" to={m.path}>
                      {t(m.translateText)}
                    </Link>
                  </li>
                )
              )}
            </ul>
          </div>
          <PrimaryButton className="register-btn">
            {t('ng k ngay')} <Icon.ArrowRight />
          </PrimaryButton>
          <button className="collapse-menu-btn" onClick={handleCollapseMenu}>
            <Icon.CollapseMenu />
          </button>
        </div>
      </div>
    </div>
  );
}

export default Header;
